.base-modal__component_hlkoj {
    position: relative;
    box-sizing: border-box;
    background: var(--color-light-bg-primary);
    margin: auto;
    flex-shrink: 0;
}
.base-modal__wrapper_hlkoj {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    outline: 0;
}
.base-modal__content_hlkoj {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;
}
.base-modal__hidden_hlkoj {
    display: none;
}
.base-modal__backdrop_hlkoj {
    z-index: 0;
}
.base-modal__appear_hlkoj,
.base-modal__enter_hlkoj {
    opacity: 0;
}
.base-modal__appearActive_hlkoj,
.base-modal__enterActive_hlkoj {
    opacity: 1;
    transition: opacity 200ms ease-in;
}
.base-modal__exit_hlkoj {
    opacity: 1;
}
.base-modal__exitActive_hlkoj,
.base-modal__exitDone_hlkoj {
    opacity: 0;
    transition: opacity 200ms ease-out;
}
