/* hash: hy6mw */
:root {
    --color-static-status-green: #13a463;
    --color-static-status-grey: #6d7986;
    --color-static-status-orange: #de6a00;
    --color-static-status-red: #d91d0b;
}
:root {
    --color-light-bg-attention-muted: #fff3e0;
    --color-light-bg-negative-muted: #feebea;
    --color-light-bg-positive-muted: #ebf9f1;
    --color-light-bg-secondary: #f3f4f5;
    --color-light-border-link: #007aff;
    --color-light-text-primary: #0b1f35;
}
:root {
    --shadow-s: 0 0 8px rgba(11, 31, 53, 0.04), 0 4px 8px rgba(11, 31, 53, 0.08);

    /* Hard */

    /* Up */

    /* Hard up */
}
:root {
    --gap-3xs: 2px;
    --gap-2xs: 4px;
    --gap-xs: 8px;
    --gap-s: 12px;
    --gap-l: 20px;
}
:root {
    --border-radius-m: 8px;
}
:root {
    --focus-color: var(--color-light-border-link);
    --arrow-transform: rotate(180deg);
}
:root {
    /* border */
    --plate-border-left: 2px;
    --plate-border: 0 0 0 1px;
    --plate-border-radius: var(--border-radius-m);

    /* width limit */

    --plate-width-limit: 560px;

    /* custom view */
    --plate-background: transparent;
    --plate-border-color: var(--color-static-status-grey);
    --plate-box-shadow: var(--shadow-s);

    /* icons */
    --plate-closer-icon: url('https://alfabank.servicecdn.ru/icons/glyph_cross_m.svg');
    --plate-arrow-icon: url('https://alfabank.servicecdn.ru/icons/glyph_chevron-down_m.svg');
}
.plate__component_kdyoa {
    position: relative;
    display: inline-flex;
    box-sizing: border-box;
    color: var(--color-light-text-primary);
    width: 100%;
    min-height: 56px;
    padding: var(--gap-s);
    outline: none
}
.plate__component_kdyoa:before {
        content: '';
        width: var(--plate-border-left);
        position: absolute;
        height: 100%;
        top: 0;
        left: 0;
    }
.plate__foldable_kdyoa {
    cursor: pointer;
}
.plate__component_kdyoa * {
    box-sizing: border-box;
}
.plate__focused_kdyoa {
    outline: 2px solid var(--focus-color);
    outline-offset: 2px;
}
.plate__inner_kdyoa {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.plate__rounded_kdyoa:before, .plate__noBorder_kdyoa:before {
        display: none;
    }
.plate__rounded_kdyoa {
    border-radius: var(--plate-border-radius);
}
.plate__shadow_kdyoa {
    box-shadow: var(--plate-box-shadow);
}
.plate__common_kdyoa {
    background-color: var(--color-light-bg-secondary);
    box-shadow: var(--plate-border) var(--color-static-status-grey)
}
.plate__common_kdyoa:before {
        background-color: var(--color-static-status-grey);
    }
.plate__negative_kdyoa {
    background-color: var(--color-light-bg-negative-muted);
    box-shadow: var(--plate-border) var(--color-static-status-red)
}
.plate__negative_kdyoa:before {
        background-color: var(--color-static-status-red);
    }
.plate__positive_kdyoa {
    background-color: var(--color-light-bg-positive-muted);
    box-shadow: var(--plate-border) var(--color-static-status-green)
}
.plate__positive_kdyoa:before {
        background-color: var(--color-static-status-green);
    }
.plate__attention_kdyoa {
    background-color: var(--color-light-bg-attention-muted);
    box-shadow: var(--plate-border) var(--color-static-status-orange)
}
.plate__attention_kdyoa:before {
        background-color: var(--color-static-status-orange);
    }
.plate__custom_kdyoa {
    background: var(--plate-background);
    box-shadow: var(--plate-border) var(--plate-border-color)
}
.plate__custom_kdyoa:before {
        background-color: var(--plate-border-color);
    }
.plate__rect_kdyoa,
.plate__noBorder_kdyoa {
    box-shadow: none;
}
.plate__isHidden_kdyoa {
    display: none;
}
.plate__leftAddons_kdyoa,
.plate__rightAddons_kdyoa {
    padding: var(--gap-2xs);
}
.plate__leftAddons_kdyoa {
    flex-shrink: 0;
    min-width: 24px;
    margin-right: var(--gap-2xs);
}
.plate__subAddons_kdyoa {
    cursor: default;
    align-self: flex-start;
}
.plate__bold_kdyoa,
.plate__light_kdyoa {
    word-break: break-word;
}
.plate__bold_kdyoa {
    font-size: 18px;
    line-height: 22px;
    font-weight: 700;
    padding: 5px var(--gap-s) 5px var(--gap-2xs);
}
.plate__light_kdyoa {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    padding: 6px var(--gap-s) 6px var(--gap-2xs);
}
.plate__content_kdyoa {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;

    max-height: 300vh;
    overflow: hidden;
    transition: max-height 2s ease-in-out;
    cursor: default
}
.plate__content_kdyoa.plate__isFolded_kdyoa {
        max-height: 0;
        transition: max-height 1s cubic-bezier(0, 1, 0, 1);
    }
.plate__contentContainer_kdyoa {
    flex: 1;
    margin-right: auto
}
.plate__contentContainer_kdyoa.plate__withoutTitle_kdyoa {
        padding-top: var(--gap-3xs);
    }
.plate__limitWidth_kdyoa {
    max-width: var(--plate-width-limit);
}
.plate__description_kdyoa {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    padding: var(--gap-2xs) var(--gap-s) var(--gap-2xs) var(--gap-2xs);
}
.plate__footer_kdyoa {
    padding: var(--gap-l) 0 var(--gap-2xs) var(--gap-2xs);
}
.plate__closer_kdyoa,
.plate__folder_kdyoa {
    display: block;
    height: 24px;
    width: 24px;
    opacity: 1;
    transition: opacity 0.2s ease;
    cursor: pointer;
    margin-left: var(--gap-s)
}
.plate__closer_kdyoa:hover, .plate__folder_kdyoa:hover {
        opacity: 0.7;
    }
.plate__closer_kdyoa:active, .plate__folder_kdyoa:active {
        opacity: 0.5;
    }
.plate__closer_kdyoa {
    background: var(--plate-closer-icon) center no-repeat;
}
.plate__folder_kdyoa {
    background: var(--plate-arrow-icon) center no-repeat;
    transition: transform 0.15s ease-in-out, opacity 0.2s ease;
    transform: var(--arrow-transform)
}
.plate__folder_kdyoa.plate__isFolded_kdyoa {
        transform: rotate(0deg);
    }
.plate__button_kdyoa:not(:last-child) {
        margin-right: var(--gap-xs);
    }
