}
:root {
    --backdrop-visible-background: var(--color-light-bg-overlay);
    --backdrop-hidden-background: transparent;
}
:root {
    --drawer-width: 500px;
}
.drawer__component_evhc1 {
    position: fixed;
    top: 0;
    height: 100%;
    width: var(--drawer-width);
    flex: 1;
    overflow: auto;
    will-change: transform;
}
.drawer__rightPlacement_evhc1 {
    right: 0;
    align-self: flex-end;
}
.drawer__leftPlacement_evhc1 {
    left: 0;
    align-self: flex-start;
}
.drawer__content_evhc1 {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;
}
.drawer__simplebar_evhc1 {
    height: 100vh;
}
/* enter */
.drawer__enterRight_evhc1 {
    transform: translateX(100%);
}
.drawer__enterLeft_evhc1 {
    transform: translateX(-100%);
}
.drawer__contentEnter_evhc1 {
    opacity: 0;
}
.drawer__backdropEnter_evhc1 {
    background-color: var(--backdrop-hidden-background);
}
.drawer__enterActive_evhc1 {
    transition: transform 0.3s ease-in-out;
    transform: translateX(0);
}
.drawer__backdropEnterActive_evhc1,
.drawer__backdropEnterDone_evhc1 {
    transition: background 0.3s ease-in-out;
    background-color: var(--backdrop-visible-background);
}
.drawer__contentEnterActive_evhc1 {
    transition: opacity 0.2s ease-in-out 0.3s;
    opacity: 1;
}
/* exit */
.drawer__exit_evhc1 {
    transform: translateX(0);
}
.drawer__backdropExit_evhc1 {
    background-color: var(--backdrop-visible-background);
}
.drawer__contentExit_evhc1 {
    opacity: 1;
}
.drawer__exitActiveRight_evhc1 {
    transition: transform 0.25s ease-in-out 0.1s;
    transform: translateX(100%);
}
.drawer__exitActiveLeft_evhc1 {
    transition: transform 0.25s ease-in-out 0.1s;
    transform: translateX(-100%);
}
.drawer__backdropExitActive_evhc1,
.drawer__backdropExitDone_evhc1 {
    transition: background 0.25s ease-in-out 0.1s;
    background-color: var(--backdrop-hidden-background);
}
.drawer__contentExitActive_evhc1 {
    opacity: 0;
    transition: opacity 0.12s ease-in-out;
}
